  z-index: 999;
  background-image: url("../images/loading.gif");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.m-question-cover::after {
  position: absolute;
  left: 50%;
  top: 45%;
  opacity: 0;
  font-size: x-large;
}

