input[type=range] {
  position: relative;
  z-index: 3;
  width: calc(100% - 12px);
  left: 6px;
  height: 48px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  -webkit-appearance: none;
  border: none;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  background: transparent;
  border: 0;
  border-radius: 24px;
  width: 100%;
  height: 16px;
}

.active input[type=range]::-webkit-slider-thumb {
  border-color: #7A3996;
}

input[type=range]::-webkit-slider-thumb {
  margin-top: -11px;
  width: 40px;
  height: 40px;
  background: white;
  border: 8px solid #8797C8;
  border-radius: 50px;
  -webkit-appearance: none;
  box-shadow: none;
  z-index: 4;
  visibility: hidden;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: transparent;
}

.flood-to-value::-webkit-slider-runnable-track {
  background: var(--track-background-fill, transparent) !important;
}

input[type=range]::-moz-range-track {
  background: transparent;
  border: 0;
  border-radius: 22px;
  width: 100%;
  height: 22px;
}

.active input[type=range]::-moz-range-thumb {
  border-color: #7A3996;
}

input[type=range]::-moz-range-thumb {
  width: 24px;
  height: 24px;
  background: white;
  border: 8px solid #8797C8;
  border-radius: 50px;
  box-shadow: none;
  z-index: 4;
  visibility: hidden;
}

.has-value .flood-to-value::-moz-range-progress {
  background-color: #D0DAE6 !important;
  height: 16px;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;
}

input[type=range]::-ms-tooltip {
  display: none;
}

input[type=range]::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 5px 0;
  color: transparent;
  width: 100%;
  height: 16px;
  border-radius: 16px;
}

input[type=range]::-ms-fill-lower {
  background: transparent;
  border-radius: 16px;
}

.has-value .flood-to-value::-ms-fill-lower {
  background-color: #D0DAE6 !important;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;
}

input[type=range]::-ms-fill-upper {
  background: transparent;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
}

.active input[type=range]::-ms-thumb {
  border-color: #7A3996;
}

input[type=range]::-ms-thumb {
  width: 24px;
  height: 24px;
  background: white;
  border: 8px solid #8797C8;
  border-radius: 50px;
  margin-top: 0px;
  box-shadow: none;
  z-index: 4;
  visibility: hidden;
  /* Needed to keep the Edge thumb centred */
}

input[type=range]:focus {
  border: none;
}

input[type=range]:focus::-ms-fill-lower {
  background: transparent;
}

.has-value .flood-to-value:focus::-ms-fill-lower {
  background: #D0DAE6 !important;
}

@supports (-ms-ime-align: auto) {
  /* Pre-Chromium Edge only styles, selector taken from https://stackoverflow.com/a/32202953/7077589 */
  input[type=range] {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}
