html {
  height: 95vh;
}

body {
  background: #FAFAFA;
  color: #212C4C;
  padding: 0;
  margin: 0 2em;
  display: flex;
  flex-direction: column;
}

body,
input,
textarea {
  font-family: "Karla", sans-serif;
}

body > form {
  min-height: 95vh;
  max-height: -webkit-fill-available;
  display: grid;
  grid-template-columns: 1fr minmax(343px, 1660px) 1fr;
  grid-template-rows: 130px auto 110px;
  grid-template-areas: ". header ." ". content ." ". footer .";
}

.header {
  grid-area: header;
  box-sizing: border-box;
  height: 130px;
  padding-bottom: 1em;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid #8797C8;
}

@media screen and (max-width: 1024px) {
  body {
    margin: 0 1em;
  }

  .header {
    height: 75px;
    padding-bottom: 0.8em;
  }

  body > form {
    grid-template-rows: 75px auto 110px;
  }
}
.surroundcontent {
  grid-area: content;
  margin-bottom: 1em;
  min-height: 66vh;
}

.footer {
  grid-area: footer;
  height: 110px;
  border-top: 1px solid #8797C8;
  padding: 1em 0;
}

.hiddencontrol {
  position: absolute;
  left: -9999px;
  width: 1px;
  height: 1px;
}

.nowrap {
  white-space: nowrap;
}

input:disabled {
  opacity: 1;
}

/* Standardises the placeholder colour - these rules cannot be combined */
::-moz-placeholder {
  color: #CCCCCC;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #CCCCCC;
  opacity: 1;
}

::placeholder {
  color: #CCCCCC;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #CCCCCC;
}

::-ms-input-placeholder {
  color: #CCCCCC;
}

sup {
  vertical-align: top;
  font-size: 0.6em;
  position: relative;
  top: -4px;
}

sub {
  vertical-align: bottom;
  font-size: 0.6em;
  position: relative;
  top: 4px;
}